import * as React from 'react';
import { Button } from '@/presentation/components/ui/Button';
import { StatCard } from '@/presentation/components/ui/StatCard';
import { Logo } from '@/presentation/components/atoms/Logo';
import { Users, Calendar, Building2, Heart, MessageSquare, TrendingUp } from 'lucide-react';

/**
 * Landing Page Component
 * Public landing page showcasing LankaConnect platform
 * Features: Hero section, community stats, and feature highlights
 */
export default function Home() {
  return (
    <div className="min-h-screen bg-white">
      {/* Fixed Header */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-white/95 backdrop-blur-md shadow-sm">
        <nav className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <div className="flex items-center gap-2 text-xl font-bold text-gray-800">
              <div className="w-10 h-10 bg-gradient-to-br from-purple-600 to-purple-800 rounded-lg flex items-center justify-center text-white">
                LC
              </div>
              <span>LankaConnect</span>
            </div>

            {/* Navigation Links - Hidden on mobile */}
            <ul className="hidden md:flex items-center gap-8">
              <li>
                <a href="#events" className="text-gray-600 hover:text-purple-600 font-medium transition-colors">
                  Events
                </a>
              </li>
              <li>
                <a href="#forums" className="text-gray-600 hover:text-purple-600 font-medium transition-colors">
                  Forums
                </a>
              </li>
              <li>
                <a href="#business" className="text-gray-600 hover:text-purple-600 font-medium transition-colors">
                  Business
                </a>
              </li>
              <li>
                <a href="#culture" className="text-gray-600 hover:text-purple-600 font-medium transition-colors">
                  Culture
                </a>
              </li>
            </ul>

            {/* Auth Buttons */}
            <div className="flex items-center gap-3">
              <Button variant="outline" size="default">
                Login
              </Button>
              <Button variant="default" size="default" className="bg-gradient-to-r from-purple-600 to-purple-800 hover:from-purple-700 hover:to-purple-900">
                Sign Up
              </Button>
            </div>
          </div>
        </nav>
      </header>

      {/* Hero Section */}
      <section className="relative bg-gradient-to-br from-purple-600 to-purple-800 text-white pt-24 pb-16 overflow-hidden">
        {/* Decorative Background Pattern */}
        <div className="absolute inset-0 opacity-10">
          <div className="absolute inset-0" style={{
            backgroundImage: 'linear-gradient(to right, white 1px, transparent 1px), linear-gradient(to bottom, white 1px, transparent 1px)',
            backgroundSize: '40px 40px'
          }} />
        </div>

        <div className="container mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
          <div className="text-center max-w-4xl mx-auto py-12">
            {/* Hero Heading */}
            <h1 className="text-4xl sm:text-5xl lg:text-6xl font-bold mb-6 leading-tight">
              Connect with Sri Lankan Communities Worldwide
            </h1>

            {/* Hero Subheading */}
            <p className="text-lg sm:text-xl mb-8 text-purple-100 max-w-2xl mx-auto">
              Building meaningful connections across the Sri Lankan diaspora through events, forums, and shared cultural experiences
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-12">
              <Button
                size="lg"
                className="bg-white text-purple-700 hover:bg-purple-50 shadow-lg hover:shadow-xl transition-all w-full sm:w-auto"
              >
                Get Started
              </Button>
              <Button
                size="lg"
                variant="outline"
                className="border-2 border-white text-white hover:bg-white/10 w-full sm:w-auto"
              >
                Learn More
              </Button>
            </div>

            {/* Community Stats */}
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 mt-16">
              <StatCard
                title="Members"
                value="12,500+"
                variant="primary"
                size="md"
                icon={<Users className="h-6 w-6" />}
              />
              <StatCard
                title="Events"
                value="450+"
                variant="primary"
                size="md"
                icon={<Calendar className="h-6 w-6" />}
              />
              <StatCard
                title="Businesses"
                value="2,200+"
                variant="primary"
                size="md"
                icon={<Building2 className="h-6 w-6" />}
              />
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="py-20 bg-gray-50">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-4">
              Everything You Need to Stay Connected
            </h2>
            <p className="text-lg text-gray-600 max-w-2xl mx-auto">
              Discover a platform built for the Sri Lankan community to thrive together
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {/* Feature 1 - Events */}
            <div className="bg-white rounded-xl p-8 shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mb-4">
                <Calendar className="h-6 w-6 text-purple-600" />
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-3">
                Discover Events
              </h3>
              <p className="text-gray-600 leading-relaxed">
                Find cultural celebrations, networking events, and community gatherings happening near you. Never miss an opportunity to connect.
              </p>
            </div>

            {/* Feature 2 - Community */}
            <div className="bg-white rounded-xl p-8 shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mb-4">
                <MessageSquare className="h-6 w-6 text-purple-600" />
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-3">
                Connect with Community
              </h3>
              <p className="text-gray-600 leading-relaxed">
                Join forums and discussions to share experiences, seek advice, and build lasting friendships with fellow Sri Lankans worldwide.
              </p>
            </div>

            {/* Feature 3 - Business */}
            <div className="bg-white rounded-xl p-8 shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mb-4">
                <Building2 className="h-6 w-6 text-purple-600" />
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-3">
                Support Local Businesses
              </h3>
              <p className="text-gray-600 leading-relaxed">
                Discover and support Sri Lankan-owned businesses in your area. From restaurants to professional services, find what you need.
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Call to Action Section */}
      <section className="py-20 bg-gradient-to-br from-purple-600 to-purple-800 text-white">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl sm:text-4xl font-bold mb-6">
            Ready to Join the Community?
          </h2>
          <p className="text-lg text-purple-100 mb-8 max-w-2xl mx-auto">
            Create your free account today and start connecting with Sri Lankans around the world
          </p>
          <Button
            size="lg"
            className="bg-white text-purple-700 hover:bg-purple-50 shadow-lg hover:shadow-xl transition-all"
          >
            Get Started Now
          </Button>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-gray-400 py-12">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            <div>
              <div className="flex items-center gap-2 text-white mb-4">
                <div className="w-8 h-8 bg-gradient-to-br from-purple-600 to-purple-800 rounded-lg flex items-center justify-center text-white text-sm font-bold">
                  LC
                </div>
                <span className="font-bold">LankaConnect</span>
              </div>
              <p className="text-sm">
                Connecting Sri Lankan communities worldwide
              </p>
            </div>

            <div>
              <h4 className="text-white font-semibold mb-3">Platform</h4>
              <ul className="space-y-2 text-sm">
                <li><a href="#events" className="hover:text-purple-400 transition-colors">Events</a></li>
                <li><a href="#forums" className="hover:text-purple-400 transition-colors">Forums</a></li>
                <li><a href="#business" className="hover:text-purple-400 transition-colors">Businesses</a></li>
                <li><a href="#culture" className="hover:text-purple-400 transition-colors">Culture</a></li>
              </ul>
            </div>

            <div>
              <h4 className="text-white font-semibold mb-3">Community</h4>
              <ul className="space-y-2 text-sm">
                <li><a href="#" className="hover:text-purple-400 transition-colors">About Us</a></li>
                <li><a href="#" className="hover:text-purple-400 transition-colors">Contact</a></li>
                <li><a href="#" className="hover:text-purple-400 transition-colors">Support</a></li>
                <li><a href="#" className="hover:text-purple-400 transition-colors">Guidelines</a></li>
              </ul>
            </div>

            <div>
              <h4 className="text-white font-semibold mb-3">Legal</h4>
              <ul className="space-y-2 text-sm">
                <li><a href="#" className="hover:text-purple-400 transition-colors">Privacy Policy</a></li>
                <li><a href="#" className="hover:text-purple-400 transition-colors">Terms of Service</a></li>
                <li><a href="#" className="hover:text-purple-400 transition-colors">Cookie Policy</a></li>
              </ul>
            </div>
          </div>

          <div className="border-t border-gray-800 mt-8 pt-8 text-center text-sm">
            <p>&copy; 2025 LankaConnect. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
}
